<template>
    <div>
        <common-header title="考场搜索" />
        <div class="search_top">
            <search placeholder="搜索考场/候选人" :searchValue="searchValue" :focusCallBack="focusCallBack" :autofocus="true" :width="12.5" :searchSubmit="searchSubmit" />
            <span class="search_cancel" @click="searchCancel">取消</span>
        </div>
        <div class="tab_warp" @click="changeTab($event)">
            <span class="tab_item  left" v-bind:class="{active:searchType===1}" data-type='1'>考场</span>
            <span class="tab_item right" v-bind:class="{active:searchType===2}" data-type='2'>候选人</span>
        </div>
        <div class="search_recent" :class="{no_data:searchRecord.length<=0}" v-show='curState==1'>
            <p class="search_recent_tite">最近搜索</p>
            <ul class="search_recent_list">
                <li class="search_recent_item" v-for="(item,index) in searchRecord" v-bind:key="index" @click="searchRecentClick(item)">
                    <span class="content">{{item}}</span>
                    <i class="icon_del" @click.stop="delSearchRecord(searchRecord.length-index)"></i>
                </li>
            </ul>
            <div class="no_data_warp">
                <div class="no_data_img">
                </div>
                <p>暂无搜索记录</p>
            </div>
        </div>
        <div class="data_scroll_warp">
            <div class="data_warp" v-show="curState==2">
                <div v-show='searchType==1'>
                    <ul v-if='examSearchRoomList.length>0' @touchend="changeNextPageEnd($event)">
                        <examitem v-for="(item,index) in examSearchRoomList" v-bind:key="item.RoomID" :examinfo='item' />
                        <li class="loading" v-if="examSearchRoomList.length > 0">
                            <span>{{loadingMsg}}</span>
                        </li>
                    </ul>

                    <div class="no_data_icon" v-else>
                        <div class="no_data_img">
                        </div>
                        <p>未搜索到考场</p>
                    </div>
                </div>
                <div v-show='searchType==2'>
                    <div class="exam-list" @touchend="changeNextPageEnd($event)" v-if='Object.keys(examDetailList.data).length>0'>
                        <!-- <div v-bind:key="key" v-for="(key , order) in Object.keys(examDetailList.data)">
                            {{key}}
                        </div> -->
                        <!-- {{Object.keys(examDetailList.data)}} -->
                        <!-- <resume-item v-bind:key="key" v-for="(key , order) in Object.keys(examDetailList.data)" :itemList="examDetailList.data[key]" :order="order" /> -->
                        <resume-item v-for="(key , order) in Object.keys(examDetailList.data)" :itemList="examDetailList.data[key]" :order="order + 1" :key="order"  />
                        <!-- <div class="loading" v-if="Object.keys(examDetailList.data).length >= 5"> -->
                        <div class="loading" v-if="Object.keys(examDetailList.data).length > 0">
                            <span>{{loadingMsg}}</span>
                        </div>
                    </div>
                    <div class="no_data_icon" v-else>
                        <div class="no_data_img">
                        </div>
                        <p>未搜索到候选人</p>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
import search from './common/SearchInput';
import examitem from './common/ExaminationItem';
import resumeitem from './common/ResumeExamItem';
import Header from './common/Header';
import { mapGetters, mapActions } from "vuex";
import { localStorageKey } from '../../static/dicConfig';
export default {
    data() {
        return {
            searchRecord: [],
            //考场默认搜索参数
            paramExam: { roomName: null, cityId: null, pageIndex: 1, pageSize: 5 },
            //候选人默认搜索参数
            paramPerson: { pageIndex: 1, pageSize: 5, roomId: '', signStatus: 0, name: '', isSearch: true, isClearCache: true },
            //搜索目标 默认为考场 1：考场 2：候选人
            searchType: 1,
            //当前状态,是否处于搜索结果显示状态 1 显示最近搜索 2 显示搜索结果
            curState: 1,
            searchValue: "",
            loadingMsg: '上拉显示更多内容',
            loadingMoreExam: true,
            loadingMorePerson: true
        }
    },
    methods: {
        ...mapActions([
            "getExamSearchRoomList",
            "getExamDetailList",
            "clearExamRoomSearchList"
        ]),
        searchCancel() {
            this.$router.push({ name: 'ExaminationRoom' });
        },
        /**
         * 初始化查询参数 
         */
        initSearchParam() {
            let self = this;
            //考场默认搜索参数
            self.paramExam = { roomName: null, cityId: null, pageIndex: 1, pageSize: 5 };
            //候选人默认搜索参数
            self.paramPerson = { pageIndex: 1, pageSize: 5, roomId: '', signStatus: 0, name: '', isSearch: true, isClearCache: true };
            self.loadingMoreExam = true;
            self.loadingMorePerson = true;
            //每次入口,清空上一次搜索记录
            self.clearExamRoomSearchList();
        },
        /**
         * @method 点击搜索 入口
         * @param {String} 搜索内容
         */
        searchSubmit(txt,event) {
            if(txt){txt=txt.trim(' ');}
            if (!txt) { return false; }
            //todo 移除搜索聚焦
            this.searchValue = txt;
            this.curState = 2;
            this.saveSearchRecord(txt);
            this.initSearchParam();
            this.getAllData();
            event.preventDefault();
        },
        /**
         * @method 点击最近搜索记录进行检索  入口
         */
        searchRecentClick(txt) {
            //todo 移除搜索聚焦
            this.searchValue = txt;
            this.curState = 2;
            this.initSearchParam();
            this.getAllData();
        },
        getAllData() {
            let self = this;
            self.loadingMsg = '正在加载...';
            if (self.searchType === 1) {
                self.paramExam.roomName = self.searchValue;
                self.getExamSearchRoomList(self.paramExam).then(
                    (data) => {
                        if (data.length >= self.paramExam.pageSize) {
                            self.loadingMoreExam = true;
                            self.loadingMsg = '上拉显示更多内容';
                        }
                        else {
                            self.loadingMoreExam = false;
                            self.loadingMsg = '没有更多数据了';
                        }
                    }
                );
            } else if (self.searchType === 2) {
                self.paramPerson.name = self.searchValue;
                self.paramPerson.successCallBack = () => {
                    if (self.paramPerson.pageIndex >= Math.ceil(self.examDetailList.total / self.paramPerson.pageSize)) {
                        self.loadingMorePerson = false;
                        self.loadingMsg = '没有更多数据了';
                    }
                    else {
                        self.loadingMorePerson = true;
                        self.loadingMsg = '上拉显示更多内容';
                    }
                }
                self.getExamDetailList(self.paramPerson);
            }
        },
        /**
         * 搜索框聚焦,切换到最近搜索记录
         */
        focusCallBack() {
            this.curState = 1;
        },
        /**
         * @method 保存搜索记录
         */
        saveSearchRecord(txt) {
            if (!txt) { return; }
            let old = JSON.parse(localStorage.getItem(`${localStorageKey.search_exam_key}${this.searchType}`));
            if (old && Array.isArray(old)) {
                let i = old.indexOf(txt);
                if (i > -1) {
                    old.splice(i, 1);
                }
                else {
                    if (old.length >= 5) {
                        //弹出第一个 先进先出
                        old.shift();
                    }
                }
            }
            else {
                old = [];
            }

            old.push(txt);
            localStorage.setItem(`${localStorageKey.search_exam_key}${this.searchType}`, JSON.stringify(old));
            this.getSearchRecord();
        },
        /**
         * @method 删除搜索记录
         */
        delSearchRecord(index) {
            let old = JSON.parse(localStorage.getItem(`${localStorageKey.search_exam_key}${this.searchType}`));
            if (old && Array.isArray(old)) {
                old.splice(index - 1, 1);
            }
            localStorage.setItem(`${localStorageKey.search_exam_key}${this.searchType}`, JSON.stringify(old));
            this.getSearchRecord();
            return false;
        },
        /**
         * @method 获取搜索记录
         */
        getSearchRecord() {
            let r = JSON.parse(localStorage.getItem(`${localStorageKey.search_exam_key}${this.searchType}`));
            if (r && Array.isArray(r)) {
                r.reverse();
            }
            else { r = []; }
            this.searchRecord = r;
        },


        /**
         * 切换tab  入口
         */
        changeTab(event) {
            let target = event.target;
            let stype = target.getAttribute("data-type");
            if (stype === '1' || stype === '2') {
                this.searchType = parseInt(stype);
                if (this.curState === 1) {
                    this.getSearchRecord();
                }
                else if (this.curState === 2) {
                    this.initSearchParam();
                    this.getAllData();
                }
            }

        },

        changeNextPageStart(ev) {
        },
        changeNextPageMove(ev) {
        },

        changeNextPageEnd(ev) {
            const eleScrollTop = window.document.documentElement.scrollTop;
            const eleScrollHeight = window.document.documentElement.scrollHeight;
            const eleClientHeight = window.document.documentElement.clientHeight;
            if (eleScrollTop + eleClientHeight >= eleScrollHeight - 5) {

                if (this.searchType === 1) {
                    if (this.loadingMoreExam) {
                        this.paramExam.pageIndex++;
                    }
                    else {
                        return;
                    }
                }
                else if (this.searchType === 2) {
                    if (this.loadingMorePerson) {
                        this.paramPerson.pageIndex++;
                    }
                    else {
                        return;
                    }
                    //清除缓存 设置为false 表示不清除
                    this.paramPerson.isClearCache = false;
                }
                this.getAllData();
            }
        },
    },
    computed: {
        ...mapGetters([
            "examSearchRoomList",
            "examDetailList"
        ])
    },
    created() {
        // this.clearExamRoomList();
        let self = this;
        self.getSearchRecord();


    },
    components: {
        search,
        'common-header': Header,
        examitem,
        'resume-item': resumeitem

    }
}
</script>

<style lang="less" scoped>
@import "../assets/css/demo.less";

.loading {
  text-align: center;
  background-color: #efeff4;
  font-size: 15/@r;
  line-height: 40/@r;
  color: #777;
}
.icon(@w,@h,@x,@y) {
  display: inline-block;
  background: url("../assets/images/icon.png");
  width: @w;
  height: @h;
  margin-right: 16/@r;
  background-position: @x @y;
  background-size: 640/@r 200/@r;
  vertical-align: middle;
  content: "";
}
.search_top {
  margin-top: 2.2rem;
  background: #fff;
  padding: 21/@r 0 21/@r 30/@r;
  box-sizing: border-box;
  .search_cancel {
    float: right;
    margin-right: 32/@r;
    font-size: 24/@r;
    color: #707c7f;
    line-height: 1.5rem;
  }
}
.tab_warp {
  border-bottom: solid 1px #e9eef0;
  background: #fff;
  height: 72/@r;
  line-height: 72/@r;
  box-sizing: border-box;
  font-size: 26/@r;
  padding: 0 80/@r;
  .tab_item {
    display: inline-block;
    height: 100%;
    width: 160/@r;
    text-align: center;
    color: #354d5a;
  }
  .tab_item.active {
    color: #5070ad;
    border-bottom: solid 1px #5070ad;
  }

  .left {
    float: left;
  }
  .right {
    float: right;
  }
}

.search_recent {
  background: #fff;
  padding: 50/@r 30/@r;
  .search_recent_tite {
    color: #909da0;
    font-size: 22/@r;
  }
  .search_recent_list {
    display: block;
    margin-top: 24/@r;
    .search_recent_item {
      font-size: 24/@r;
      color: #354d5a;
      border-bottom: solid 1px #e9eef0;
      padding: 26/@r 0;
      &:before {
        .icon(24/@r,24/@r,-50/@r,-9/@r);
        margin-top: -0.05rem;
      }

      .icon_del {
        cursor: pointer;
        .icon(17/@r,17/@r,-151/@r,-50/@r);
        float: right;
        margin: 8/@r 0 0 0;
      }
    }
  }
  .no_data_warp {
    display: none;
  }
}
.search_recent.no_data {
  background: none;
  .search_recent_list {
    display: none;
  }
  .no_data_warp {
    display: block;
    .no_data_img {
      background: url("../assets/images/icon.png") -200/@r -10/@r;
      background-size: 640/@r 200/@r;
      width: 120/@r;
      height: 85/@r;
      margin: 0 0 20/@r 8/@r;
    }
    width: 150/@r;
    font-size: 22/@r;
    color: #909da0;
    margin: 160/@r auto;
  }
}
.no_data_icon {
  text-align: center;
  .no_data_img {
    background: url("../assets/images/icon_no_data.png") no-repeat;
    width: 350/@r;
    height: 170/@r;
    background-size: contain;
    margin: 0 auto 60/@r auto;
  }
  font-size: 22/@r;
  color: #909da0;
  padding: 160/@r 0;
}
</style>




