<script>
export default {
	name: 'ResumeItem',
	data() {
		return {
			imgPathObj: {
				0: require('../../assets/images/undetermined.png'),
				1: require('../../assets/images/pass.png'),
				2: require('../../assets/images/noPass.png'),
			},
			imgMainPath: 'http://campusimage.zhaopin.com/',
			defaultSrc: require('../../assets/images/defaultPhoto.png'),
			dataSrc: ''
		}
	},
	props: {
		itemList: {
			type: Array,
			default() {
				return {}
			}
		},
		order: {
			type: Number,
			default() {
				return '';
			}
		}
	},
	methods: {
		loadImage(path) {
			const image = new Image();
			image.src = path;
			image.onload = () => {
				this.dataSrc = path;
			}
			return true
		}
	}
}
</script>
<template>
	<div class="item_warp">
		<div v-for="item in itemList" v-bind:key="item.campusId">
			<router-link class="item clearfix" :to="{
					name: 'ResumeDetails', 
					query: {campusId:item.CampusId , postID: item.PostId , roomId: item.RoomId}
				}" tag="div">
				<!-- <div class="tip">{{item.TimeRange}}</div> -->
				<!-- <div class="tip"></div> -->
				<img :src="loadImage(imgMainPath + item.PhotoSrc) && dataSrc || defaultSrc" class="fl photo">
				<div class="item-left">
					<div class="person-info-top ">
						<span class="name ">{{item.Name}}</span>
						<span class="sex ">{{item.Gender}}</span>
						<span class="education ">{{item.Degree}}</span>
						<!-- <span class="school-level ">{{item.SchoolType}}</span> -->
						<!-- <span class="sign fl">{{item.SignStatus === 1 ? '已签到' : '未签到' }}</span> -->
						<span class="order fr">{{item.SeatNo < 10 ? '0' + item.SeatNo : item.SeatNo}}</span>
					</div>
					<div class="person-info-middle">
						<span>{{item.SchoolName}}</span>
						<span>|</span>
						<span>{{item.Major}}</span>
					</div>
					<div class="person-info-bottom clearfix">
						<span class="fl">{{item.CommentCount}}条评语</span>
						<!-- <div class="contact fl clearfix" v-if="item.SignStatus !== 1">
							<a class="fl" :href="'tel:' + item.Phone"></a>
							<a class="fl" :href="'sms:' + item.Phone"></a>
						</div> -->
					</div>
				</div>
				<img :src="imgPathObj[item.SiftStatus]" class="pass-flag">
			</router-link>

			<div class="exam_resume_item_room">
				<div>
					<p class="first">
						<span class="room"> {{item.RoomName}}</span>
						<router-link class="entry_exam" :to="{
						name:'ResumeList',query: { roomid: item.RoomId }
						}" tag="span">
							<span>进入考场</span>
						</router-link>
					</p>
					<p class="second">
						<span class="time">
							<span>{{item.DateStart}}</span>
						</span>
					</p>
				</div>
			</div>
		</div>
	</div>
</template>
<style lang="less" scoped>
@r: 40rem;
.tip {
  font-size: 22/@r;
  color: #707c7f;
  line-height: 50/@r;
  margin-left: 30/@r;
  i {
    display: block;
    margin-left: 35/@r;
    margin-right: 5/@r;
    margin-top: 6/@r;
    width: 25/@r;
    height: 25/@r;
    background: url("../../assets/images/icon.png") no-repeat;
    background-size: 610/@r;
    background-position: -76/@r -10/@r;
  }
}

.item {
  position: relative;
  background-color: white;
  padding: 30/@r 0 30/@r 30/@r;
  color: #99a5a7;
  font-size: 20/@r;
  img {
    width: 120/@r;
  }
  .photo {
    width: 90/@r;
    height: 110/@r;
  }
  .item-left {
    margin-left: 115/@r;
    // margin-top: -110/@r;
    .person-info-top {
      //   margin-top: -10/@r;
      margin-bottom: 15/@r;
      font-size: 0;
      span {
        margin-right: 10/@r;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        vertical-align: middle;
      }
      .name {
        font-size: 28/@r;
        // font-weight: 600;
        margin-right: 10/@r;
        color: black;
        margin-right: 25/@r;
        // width: 116/@r;
        vertical-align: middle;
      }
      .sex,
      .education,
      .school-level {
        display: inline-block;
        color: #707c7f;
        font-size: 24/@r;

        // margin-top: 6/@r;
      }
      .school-level {
        width: 5em;
      }
      .education {
        width: 5em;
      }
      .sign {
        color: #ff3600;
        border: 1px solid #ff3600;
        padding: 0 8/@r;
        border-radius: 15/@r;
        font-size: 20/@r;
  
      }
      .order {
		color: #f29c00;
		font-size: 26/@r;
		margin-right: 45/@r;
      }
    }
    .person-info-middle {
      margin-bottom: 10/@r;
    }
    .person-info-bottom {
      .contact {
        a {
          display: block;
          width: 30/@r;
          height: 28/@r;
        }
        a:first-child {
          background: url("../../assets/images/icon.png") no-repeat;
          background-position: -224/@r -112/@r;
          background-size: 720/@r;
          margin-left: 256/@r;
          margin-top: -2/@r;
        }
        a:last-child {
          background: url("../../assets/images/icon.png") no-repeat;
          background-position: -288/@r -112/@r;
          background-size: 720/@r;
          margin-left: 30/@r;
          margin-top: -2/@r;
        }
      }
    }
  }
  .pass-flag {
    position: absolute;
    left: 584/@r;
    top: 0;
    width: 57/@r;
    height: 61/@r;
  }
}
.item_warp {
  margin-top: 16/@r;
  &:first-child {
    margin-top: 0;
  }
}
</style>

<style lang="less" scoped>
@import "../../assets/css/examinationItem.less";
</style>



